import Table from 'react-bootstrap/Table';
import data1 from '../data/servicedata'
import { useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Routes, Route, Link, useNavigate, Outlet } from 'react-router-dom';
import { Navbar, Container, Nav, Row, Col} from 'react-bootstrap';
import Detail from '../pages/detail';
import '../App.css';
import Search from '../pages/search';

function Service() {
    let [data] = useState(data1);
  return (
    <div>
      <Search />
      <Routes> 
        <Route path="/" element={
          <div>
           <table>
            <thead>
              <tr>
                <th id="td_left">
                  검색결과
                </th>
              </tr>
              <tr>
                <th>요청번호</th>
                <th>요청유형</th>
                <th>요청제목</th>
                <th>요청자</th>
                <th>요청상태</th>
                <th>요청일시</th>
                <th>종료일시</th>
              </tr>
            </thead>
            <tbody >
              {
                  data.map(function(a,i)
                  {
                      return(
                          <DataMap data={data} i={i} />
                      )
                  })
              }
            </tbody>
           </table>
         </div>
        } />
        <Route path="/detail/:id" element={<Detail data={data}/>} />;
        <Route path="*" element={<div>없는SR</div>} /> 

      </Routes>
       </div>
      );
}

function DataMap(props){
  let navigate = useNavigate();
  let i = props.i;
  return(
          <tr onClick={()=>{navigate('/service/detail/'+props.i)}}>
              <td>{props.data[i].id}</td>
              <td>{props.data[i].type}</td>
              <td id="td_left">{props.data[i].title}</td>
              <td>{props.data[i].name}</td>
              <td>{props.data[i].state}</td>
              <td>{props.data[i].startday}</td>
              <td>{props.data[i].endday}</td>
            </tr>
          )
}


export default Service