import React from 'react'
import styled from 'styled-components';
import { Line } from 'react-chartjs-2';
import { Chart, registerables } from "chart.js"
Chart.register(...registerables)

const data = {
    labels: ['서버', '네트워크', 'DB', 'WEB', 'OA', '계정'],
    datasets: [
      {
        type: 'bar',
        label: '인시던트요청',
        backgroundColor: 'rgb(70, 38, 182)',
        data: [17, 12, 26, 14, 13, 6, 11],
      },
    ],
  };

const options = {
  spanGaps: true,
  maxBarThickness: 30,
  grouped: true,
  interaction: {
    mode: 'index',
  },
  plugins: {
    legend: {
      labels: {
        usePointStyle: true,
        padding: 10,
        font: {
          family: "'Noto Sans KR', 'serif'",
          lineHeight: 1,
        },
      }
    }
  }
};

const Inchart = () => {
  return (
    <Container>
      <Line type="line" data={data} options={options} />
    </Container>
  );
};

export default Inchart;

const Container = styled.div`
  width: 25vw;
  max-width: 450px;
  height: 20vw;
  max-height: 230px;
`;