import React from 'react'
import styled from 'styled-components';
import { Line } from 'react-chartjs-2';
import { Chart, registerables } from "chart.js"
Chart.register(...registerables)

const data = {
    labels: ['서버', '네트워크', 'DB', 'WEB', 'OA', '계정'],
    datasets: [
      {
        type: 'bar',
        label: '변경요청',
        backgroundColor: 'rgb(216, 255, 98)',
        data: [27, 33, 21, 4, 4, 1, 42],
      },
    ],
  };

const options = {
  spanGaps: true,
  maxBarThickness: 30,
  grouped: true,
  interaction: {
    mode: 'index',
  },
  plugins: {
    legend: {
      labels: {
        usePointStyle: true,
        padding: 10,
        font: {
          family: "'Noto Sans KR', 'serif'",
          lineHeight: 1,
        },
      }
    }
  }
};

const Chchart = () => {
  return (
    <Container>
      <Line type="line" data={data} options={options} />
    </Container>
  );
};

export default Chchart;

const Container = styled.div`
  width: 25vw;
  max-width: 450px;
  height: 20vw;
  max-height: 230px;
`;